// adventure posts

.adventure-embed {
    margin-left: auto;
    margin-right: auto;
    width: 1120px;
    h2 {
        color: #2f1339;
        line-height: 1.25;
        letter-spacing: 2px;
        text-transform: uppercase;
        font-family: 'Novecento Sans Web';
    }
    ul {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: 2fr;
        grid-gap: 20px 20px;
        list-style-type: none;
        margin-left: 0;
        padding-left: 0;
        width: 100%;
        height: 600px;
    }
    li {
        line-height: 1;
        overflow: hidden;
        position: relative;
        width: 100%;
        &:first-child {
            grid-column: 1/span 2;
            grid-row: 1/span 2;
            font-size: 2.8rem;
        }
        &:nth-child(2) {
            grid-column: 3/span 2;
            grid-row: 1/span 1;
            font-size: 2.2rem;
        }
        &:nth-child(3) {
            font-size: 1.4rem;
        }
        &:nth-child(4) {
            font-size: 1.4rem;
        }

    }
    .adventure-wrapper {
        img {
            left: 50%;
            max-height: 620px;
            max-width: none;
            position: absolute;
            top: 75%;
            transform: translate(-50%, -75%);
            width: auto;
            background:rgba(0,0,0,.35);
        }
        &:after {
            background: rgba(0,0,0,.35);
            content: "";
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 1;  
        }
    }

.adventure-info {
    margin: 1.5rem;
    position: absolute;
    z-index: 2;
}

.adventure-title {
        font-family: 'Merriweather Web';
        font-weight: normal;
        margin-top: 24px;
        // z-index: 2;
        text-decoration: none;
        color: #FFF;
        letter-spacing: 2px;
        text-transform: none;
    }
    a {
        text-decoration: none;
        color: #FFF;
        &:hover {
            text-decoration: none;
            color: #f0e4d1;
        } }
}

.adventure-button {
    border: 1px solid #FFF;
    color: #FFF;
    text-decoration: none;
    line-height: 1;
    font-size: 0.9rem;
    padding: 0.75rem 1.5rem;
    text-transform: uppercase;
    &:hover {
        background-color: #248a83;
        border: 1px solid #248a83;
        color: #FFF;
    } 
} 

