/* Adventure Archive Styles */
.post-type-archive-adventure {
  .content-area {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
 
  .page-title {
    font-size: 2.4rem;
  }
  .adventure-grid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1.5rem;
    margin-bottom: 4.5rem;
    .adventure-grid-item {
      position: relative;
      &:before {
        content: "";
        float: left;
        padding-top: 100%;
      }
      h2 {
        line-height: 1.25;
        font-size: 3rem;
        font-weight: 400;
        margin-bottom: 4.5rem;
        text-transform: none;
        a {
          font-family: 'Merriweather Web';
          text-transform: none;
          color: #FFF;
          &:hover {
            color: #f0e4d1;
          }
        }
        font-family: Merriweather Web,serif;
        font-size: 48px;
        font-size: 3rem;
        font-weight: 400;
        margin-bottom: 4.5rem;
        text-transform: none;
      }
      .adventure-item-wrapper {
        bottom: 0;
        left: 0;
        overflow: hidden;
        position: absolute;
        right: 0;
        top: 0;
        &:after {
          background: rgba(0,0,0,.35);
          content: "";
          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          width: 100%;
          z-index: 1;
        }
        img {
          left: 50%;
          max-height: 550px;
          max-width: none;
          width: auto;
          position: absolute;
          top: 50%;
          transform: translate(-50%,-50%);
          z-index: 1;
        }
        .adventure-info {
          margin: 4.5rem 1.5rem;
          position: absolute;
          z-index: 2;
        }
      }
    }
  }
}

